@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-left {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

.icon-right {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("../img/ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("../fonts/slick.eot");
  src: url("../fonts/slick.eot?#iefix") format("embedded-opentype"), url("../fonts/slick.woff") format("woff"), url("../fonts/slick.ttf") format("truetype"), url("../fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  margin-top: -10px;
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before, .slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dots {
  position: absolute;
  bottom: 22px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.slick-dots li button {
  display: block;
  padding: 3px;
  background: none;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: red;
  cursor: pointer;
}

.slick-dots li button:before {
  content: "";
  display: block;
  height: 10px;
  width: 10px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #979795;
  transition: all 0.1s ease;
}

.slick-dots li button:hover:before {
  background: #4E4E4E;
}

.slick-dots li.slick-active button:before {
  background: #4E4E4E;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  font-family: Helvetica, sans-serif;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

.tabs {
  margin: 0 auto;
  text-align: center;
}

.tabs__buttons .active {
  background-color: #14a9e7;
  color: #ffffff;
}

.tabs__buttons button {
  color: #14a9e7;
  background-color: #ffffff;
  border: 1px solid #14a9e7;
  border-radius: 5px;
  padding: 10px;
}

.tabs__buttons button:focus {
  background-color: #14a9e7;
  color: #ffffff;
  transition: 0.4s ease-in-out;
}

.tabs__tab {
  padding-top: 10px;
  position: relative;
}

.slides {
  display: flex;
  max-width: 600px;
  margin: 0 auto;
}

.slide {
  margin: 0 5px;
  border: 1px solid #14a9e7;
  border-radius: 5px;
  text-align: left;
}

.slide__img {
  width: 100%;
  height: 180px;
  border-bottom: 1px solid #14a9e7;
}

.slide__title {
  padding: 0 10px;
  color: #000000;
  font-size: 35px;
}

.slide__desc {
  padding: 0 10px;
  font-size: 15px;
}

.slide__info {
  padding: 0 10px;
  color: #bbb;
  font-size: 14px;
}

.slide__button {
  width: 100%;
  display: block;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background-color: #14a9e7;
  text-transform: uppercase;
  font-size: 25px;
  padding: 10px;
}

.slide__button:hover {
  background-color: #0099ff;
}

.slider {
  width: 40%;
  margin: 0 auto;
}

.slick-prev,
.slick-next {
  width: 30px;
  height: 30px;
  top: 50%;
  opacity: 1;
  border-radius: 4px;
}

.slick-prev {
  position: absolute;
  left: -9%;
  background-color: #14a9e7;
}

.slick-prev:hover, .slick-prev:focus {
  background-color: #0099ff;
}

.slick-prev:before {
  position: absolute;
  content: '';
  opacity: 1;
  top: 19%;
  left: 27%;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-radius: 3px;
  border-right: 10px solid #ffffff;
}

.slick-next {
  right: -9%;
  background-color: #14a9e7;
}

.slick-next:hover, .slick-next:focus {
  background-color: #0099ff;
}

.slick-next:before {
  position: absolute;
  content: '';
  opacity: 1;
  top: 19%;
  left: 36%;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-radius: 3px;
  border-left: 10px solid #ffffff;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  position: relative;
  background-color: #fefefe;
  margin: 15% auto;
  padding: 0 20px;
  border: 1px solid #888;
  width: 50%;
  min-height: 275px;
  border-radius: 5px;
}

.close {
  position: absolute;
  right: -25px;
  top: -40px;
  color: #ffffff;
  font-size: 28px;
  font-weight: 400;
}

.close:hover, .close:focus {
  color: #14a9e7;
  text-decoration: none;
  cursor: pointer;
}

.sliderModal {
  width: 100%;
}

.modalSlide {
  display: flex;
  justify-content: space-between;
}

.modalSlide__img {
  width: 50%;
  height: 230px;
}

.modalSlide__img img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.text__title {
  font-size: 30px;
}

.text__list {
  font-size: 20px;
}

.text__info {
  font-size: 14px;
  color: #bbb;
}

.bounceInDown {
  animation-name: bounceInDown;
}

.slider-paging-number {
  position: absolute;
  background-color: #14a9e7;
  display: inline-block;
  padding: 2px 6px;
  border-radius: 4px;
  bottom: 8px;
  right: 8px;
}

.slider-paging-number li {
  color: #ffffff;
  font-size: 17px;
}

.slider-paging-number li:nth-child(1n+2) {
  display: none;
}
